import ko from 'knockout'
import template from './list.html'
/* global u */
function init () {
  var dt = new u.DataTable({
    meta: {
      id: '',
      name: '',
      projName: '',
      endtime: '',
      material: '',
      createtime: '',
      state: ''
    }
  })
  let vm = {
    id: ko.observable('id'),
    curStateIndex: ko.observable(1),
    totalCount: ko.observable(12),
    stateTabsItems: ko.observableArray([
      {
        title: '',
        state: 1,
        num: 2
      },
      {
        title: '',
        state: 2,
        num: 10
      },
      {
        title: '',
        state: 3,
        num: 7
      },
      {
        title: '',
        state: 4,
        num: 1
      },
      {
        title: '',
        state: 5,
        num: 2
      },
      {
        title: '',
        state: 6,
        num: 0
      },
      {
        title: '',
        state: 7,
        num: 0
      }
    ]),
    stateTabsHandler: function (item) {
      console.log(item)
    },
    rows: dt.rows,
    columns: [
      {
        title: '',
        width: 70,
        type: 'index'
      },
      {
        title: '',
        width: '15%',
        field: 'projName'
      },
      {
        title: '',
        width: '15%',
        field: 'material'
      },
      {
        title: '',
        width: '15%',
        field: 'createtime',
        dataType: 'date'
      },
      {
        title: '',
        width: '15%',
        field: 'endtime',
        dataType: 'date'
      },
      {
        title: '',
        width: '15%',
        field: 'state',
        type: 'render',
        renderFn: function (row) {
          switch (row.getValue('state')) {
            case 1: return ''
            case 2: return ''
            case 3: return ''
            case 4: return ''
            default: return ''
          }
        }
      },
      {
        field: 'id',
        title: '',
        type: 'operation',
        operationList: [
          {
            title: '',
            click: function (row) {

            },
            visible: function (row) {
              return row.getValue('state') === 1
            }
          },
          {
            title: '',
            click: function (row, evt) {
              row._disabled(true)
              return false
            },
            visible: function (row) {
              return !row._disabled()
            }
          },
          {
            title: '',
            click: function (row, evt) {
              row._disabled(true)
              return false
            },
            visible: function (row) {
              return !row._disabled()
            }
          },
          {
            title: '',
            click: function (row, evt) {
              row._disabled(true)
              return false
            },
            visible: function (row) {
              return !row._disabled()
            }
          },
          {
            title: '',
            click: function (row, evt) {
              row._disabled(true)
              return false
            },
            visible: function (row) {
              return !row._disabled()
            }
          }
        ]
      }
    ]
  }
  setTimeout(() => {
    dt.setSimpleData([{
      id: '1',
      name: '1',
      projName: '20170212',
      endtime: '2017-12-06',
      material: '',
      createtime: '2017-05-06',
      state: 1
    }, {
      id: '1',
      name: '1',
      projName: '20170213',
      endtime: '2017-12-06',
      material: '',
      createtime: '2017-05-06',
      state: 3
    }, {
      id: '1',
      name: '1',
      projName: '20170214',
      endtime: '2017-12-06',
      material: '',
      createtime: '2017-05-06',
      state: 2
    }, {
      id: '1',
      name: '1',
      projName: '20170215',
      endtime: '2017-11-06',
      material: '',
      createtime: '2017-03-16',
      state: 4
    }, {
      id: '1',
      name: '1',
      projName: '20170216',
      endtime: '2017-12-06',
      material: '',
      createtime: '2017-05-06',
      state: 2
    }, {
      id: '1',
      name: '1',
      projName: '20170212',
      endtime: '2017-12-06',
      material: '',
      createtime: '2017-05-06',
      state: 2
    }])
  })
  ko.applyBindings(vm, document.getElementById('app'))
}
export default {
  init: init,
  template: template
}



